import React, { Component } from "react";
import { Modal, Button, Table } from "antd";

const { Column } = Table;

export default class ModalGioHang extends Component {
  state = { visible: false };

  render() {
    const { gioHang, tongSoLuong, deleteItem, tangGiam } = this.props;
    return (
      <div className="text-right">
        <Button
          type="primary"
          danger
          className="flex-right"
          style={{ marginBottom: 20 }}
          onClick={() => this.setState({ visible: true })}
        >
          Gi hng ({tongSoLuong})
        </Button>
        <Modal
          title="Gi hng"
          centered
          visible={this.state.visible}
          onOk={() => this.setState({ visible: false })}
          onCancel={() => this.setState({ visible: false })}
          width={1000}
        >
          <Table
            dataSource={[...gioHang]}
            rowKey="maSP"
            pagination={{ pageSize: 4 }}
          >
            <Column title="M SP" dataIndex="maSP" key="M sn phm" />
            <Column
              title="Hnh nh"
              dataIndex="hinhAnh"
              key="Hnh nh"
              render={(hinhAnh) => (
                <img src={hinhAnh} alt="hinhanh" style={{ width: 100 }} />
              )}
            />
            <Column title="Tn" dataIndex="tenSP" key="Tn" />
            <Column
              title="S lng"
              dataIndex="soLuong"
              key="S Lng"
              width={170}
              render={(text, row) => (
                <>
                  <Button onClick={() => tangGiam(row["maSP"] * -1)}>-</Button>
                  {text}
                  <Button onClick={() => tangGiam(row["maSP"])}>+</Button>
                </>
              )}
            />
            <Column
              title="n gi"
              dataIndex="giaBan"
              render={(giaBan) => giaBan.toLocaleString()}
              key="n gi"
            />
            <Column
              title="Thnh tin"
              key="Thnh tin"
              render={(text, row) =>
                (row["soLuong"] * row["giaBan"]).toLocaleString()
              }
            />
            <Column
              title="Xa"
              key="Xa"
              render={(text, row) => (
                <Button
                  danger
                  onClick={() => {
                    deleteItem(row["maSP"]);
                  }}
                >
                  Xa
                </Button>
              )}
            />
          </Table>
        </Modal>
      </div>
    );
  }
}
